/**
 * Apple ÏÜåÏÖú Î°úÍ∑∏Ïù∏ Íµ¨ÌòÑ
 *
 * Ïù¥ ÌååÏùºÏùÄ Apple Sign InÏùÑ ÌÜµÌïú Ïù∏Ï¶ù Íµ¨ÌòÑÏùÑ Îã¥ÎãπÌï©ÎãàÎã§.
 */

import { SupabaseClient } from "@supabase/supabase-js";
import { Database } from "@/types/supabase";
import {
  AuthResult,
  OAuthProviderConfig,
  SocialAuthError,
  SocialAuthErrorCode,
  SocialAuthOptions,
} from "./types";

/**
 * Apple OAuth ÏÑ§Ï†ï
 */
export function getAppleConfig(): OAuthProviderConfig {
  // ÌôòÍ≤ΩÏóê Îî∞Î•∏ Í∏∞Î≥∏ URL ÏÑ§Ï†ï
  const getBaseUrl = () => {
    // Í∞úÎ∞ú ÌôòÍ≤ΩÏóêÏÑúÎäî ÌôòÍ≤ΩÎ≥ÄÏàò Ïö∞ÏÑ† ÏÇ¨Ïö©
    if (process.env.NODE_ENV === "development") {
      const siteUrl = process.env.NEXT_PUBLIC_SITE_URL;
      if (siteUrl) {
        return siteUrl;
      }
      // ÌôòÍ≤ΩÎ≥ÄÏàòÍ∞Ä ÏóÜÏúºÎ©¥ Í∏∞Î≥∏ Í∞úÎ∞ú URL
      return "http://localhost:3100";
    }

    // ÌîÑÎ°úÎçïÏÖò ÌôòÍ≤Ω
    if (typeof window !== "undefined") {
      return window.location.origin;
    }

    // ÏÑúÎ≤Ñ ÏÇ¨Ïù¥ÎìúÏóêÏÑúÎäî ÌôòÍ≤Ω Î≥ÄÏàò ÎòêÎäî Í∏∞Î≥∏Í∞í ÏÇ¨Ïö©
    if (process.env.NEXT_PUBLIC_SITE_URL) {
      return process.env.NEXT_PUBLIC_SITE_URL;
    }

    // ÌîÑÎ°úÎçïÏÖò Í∏∞Î≥∏Í∞í
    return "https://www.picnic.fan";
  };

  return {
    clientId: process.env.NEXT_PUBLIC_APPLE_CLIENT_ID || "",
    clientSecretEnvKey: "APPLE_CLIENT_SECRET",
    defaultScopes: [
      "email",
      "name",
    ],
    additionalConfig: {
      // Apple ÌäπÌôî ÏÑ§Ï†ï
      responseMode: "form_post",
      responseType: "code id_token",
      usePopup: false,
      // ÏõπÏö© ÏÑúÎπÑÏä§ ID (Apple Developer ConsoleÏóêÏÑú ÏÑ§Ï†ï)
      webClientId: process.env.NEXT_PUBLIC_APPLE_WEB_CLIENT_ID ||
        process.env.NEXT_PUBLIC_APPLE_CLIENT_ID || "",
      // ÌåÄ ID (Apple Developer ConsoleÏóêÏÑú ÌôïÏù∏)
      teamId: process.env.APPLE_TEAM_ID || "",
      // ÌÇ§ ID (Apple Developer ConsoleÏóêÏÑú ÏÉùÏÑ±Ìïú ÌîÑÎùºÏù¥Îπó ÌÇ§Ïùò ID)
      keyId: process.env.APPLE_KEY_ID || "",
      // Î¶¨ÎîîÎ†âÏÖò URI ÏÑ§Ï†ï - ÌôòÍ≤ΩÏóê Îî∞Îùº ÎèôÏ†ÅÏúºÎ°ú ÏÑ§Ï†ï
      redirectUri: `${getBaseUrl()}/api/auth/apple`,
    },
  };
}

/**
 * Apple Î°úÍ∑∏Ïù∏ Íµ¨ÌòÑ
 *
 * @param supabase Supabase ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏
 * @param options Ïù∏Ï¶ù ÏòµÏÖò
 * @returns Ïù∏Ï¶ù Í≤∞Í≥º
 */
export async function signInWithAppleImpl(
  supabase: SupabaseClient<Database>,
  options?: SocialAuthOptions,
): Promise<AuthResult> {
  try {
    // ÏôÑÏ†ÑÌûà ÌëúÏ§Ä Supabase OAuth ÏÇ¨Ïö©
    const config = getAppleConfig();
    const scopes = options?.scopes || config.defaultScopes;

    // Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄÏóê Î¶¨Îã§Ïù¥Î†âÌä∏ URL Ï†ÄÏû• (ÏΩúÎ∞± ÌõÑ ÎêòÎèåÏïÑÏò¨ ÏúÑÏπò)
    if (typeof localStorage !== "undefined") {
      const returnUrl = options?.additionalParams?.return_url ||
        window.location.pathname;
      localStorage.setItem("auth_return_url", returnUrl);
    }

    console.log("‚úÖ ÌëúÏ§Ä Supabase Apple OAuth ÏãúÏûë");

    // ÌëúÏ§Ä Supabase OAuth ÏÇ¨Ïö©
    const { data, error } = await supabase.auth.signInWithOAuth({
      provider: "apple",
      options: {
        scopes: scopes.join(" "),
        redirectTo: `${window.location.origin}/auth/callback`,
      },
    });

    console.log("üçé Apple OAuth ÏÑ§Ï†ï:", {
      redirectTo: `${window.location.origin}/auth/callback`,
      currentOrigin: window.location.origin,
      nodeEnv: process.env.NODE_ENV,
      siteUrl: process.env.NEXT_PUBLIC_SITE_URL,
    });

    if (error) {
      console.error("‚ùå Supabase Apple OAuth Ïò§Î•ò:", error);
      throw new SocialAuthError(
        SocialAuthErrorCode.AUTH_PROCESS_FAILED,
        error.message,
        "apple",
        error,
      );
    }

    console.log("‚úÖ Supabase Apple OAuth ÏÑ±Í≥µ, Î¶¨Îã§Ïù¥Î†âÏÖò Ï§ë...");

    return {
      success: true,
      provider: "apple",
      message: "Apple Î°úÍ∑∏Ïù∏ Î¶¨ÎîîÎ†âÏÖò Ï§ë...",
    };
  } catch (error) {
    if (error instanceof SocialAuthError) {
      throw error;
    }

    throw new SocialAuthError(
      SocialAuthErrorCode.AUTH_PROCESS_FAILED,
      error instanceof Error ? error.message : "Ïïå Ïàò ÏóÜÎäî Apple Î°úÍ∑∏Ïù∏ Ïò§Î•ò",
      "apple",
      error,
    );
  }
}

/**
 * Apple ID ÌÜ†ÌÅ∞ ÌååÏã±
 *
 * @param idToken AppleÏóêÏÑú Î∞òÌôòÎêú ID ÌÜ†ÌÅ∞
 * @returns ÌååÏã±Îêú ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥
 */
export function parseAppleIdentityToken(idToken: string): Record<string, any> {
  try {
    const payload = idToken.split(".")[1];
    const decodedPayload = Buffer.from(payload, "base64").toString("utf8");
    return JSON.parse(decodedPayload);
  } catch (error) {
    console.error("Apple ID ÌÜ†ÌÅ∞ ÌååÏã± Ïò§Î•ò:", error);
    return {};
  }
}

/**
 * Apple ÏÇ¨Ïö©Ïûê ÌîÑÎ°úÌïÑ Ï†ïÎ≥¥ Ï†ïÍ∑úÌôî
 *
 * @param payload Apple ID ÌÜ†ÌÅ∞ÏóêÏÑú Ï∂îÏ∂úÌïú ÌéòÏù¥Î°úÎìú
 * @param userData Ï∂îÍ∞Ä ÏÇ¨Ïö©Ïûê Îç∞Ïù¥ÌÑ∞ (ÏµúÏ¥à Î°úÍ∑∏Ïù∏ Ïãú Ï†úÍ≥µ)
 * @returns ÌëúÏ§ÄÌôîÎêú ÏÇ¨Ïö©Ïûê ÌîÑÎ°úÌïÑ
 */
export function normalizeAppleProfile(
  payload: any,
  userData?: any,
): Record<string, any> {
  // Ï∞∏Í≥†: AppleÏùÄ ÏµúÏ¥à Î°úÍ∑∏Ïù∏ ÏãúÏóêÎßå Ïù¥Î¶ÑÍ≥º Ïù¥Î©îÏùºÏùÑ Ï†úÍ≥µÌïòÎØÄÎ°ú
  // Ïù¥ Ï†ïÎ≥¥Îäî Ï†ÄÏû•Ìï¥ÎëêÍ≥† Ïû¨ÏÇ¨Ïö©Ìï¥Ïïº Ìï©ÎãàÎã§.
  const profile: Record<string, any> = {
    id: payload.sub || "",
    email: payload.email || "",
    email_verified: payload.email_verified || false,
    provider: "apple",
  };

  // ÏµúÏ¥à Î°úÍ∑∏Ïù∏ Ïãú Ï†ÑÎã¨Îêú ÏÇ¨Ïö©Ïûê Îç∞Ïù¥ÌÑ∞Í∞Ä ÏûàÎäî Í≤ΩÏö∞
  if (userData && userData.name) {
    profile.name = [userData.name.firstName, userData.name.lastName]
      .filter(Boolean)
      .join(" ");
    profile.givenName = userData.name.firstName || "";
    profile.familyName = userData.name.lastName || "";
  }

  return profile;
}

